#projectPage{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    max-width: 80vw;
    margin: 20px auto;
}



#leftContainer{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}


#rightContainer{
    -ms-flex: 2 1;
        flex: 2 1;
    background-color: black;
    color: white;
    margin: 0 0 50px 50px;
}

#slideshow{
    display: -ms-flexbox;
    display: flex;
    margin: 0 auto;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 4vh;
}

.projectImg{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 40vh;
    max-width: 33vw;
    margin: auto;
    border: solid 3px rgb(11, 32, 78);
}

#arrow{
    height: 5vh;
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 15px;
}

.pagination{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 15px;
    max-width: 15px;
    margin: 0 auto;
    background-color: rgb(255, 255, 255);
    border: solid 1px blue;
    border-radius: 50%;
}
.pagination.active{
    background-color: blue;
    
    /* transform: scale(1.2) */
}

#pagination{
    max-width: 10vw;
    margin: 0 auto;
    -ms-flex-line-pack: middle;
        align-content: middle;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding-bottom: 20px;
}

#collapseDiv{
    height: 100px;
    width: 200px;
}

#projectTitle{
    font-size: 1.6em;
    font-weight: bold;
    margin-bottom: 10px;
}
.accordion{
    font-size: 1.4em;
    color: white;
}

.accordionDiv{
    background-color: rgb(245, 245, 245);
    font-size: 0.7em;
    padding: 6px;
    margin-top: 10px;
    color: black;
    border-radius: 5px;
    border: solid 1px black;
}

.react-sanfona-item:hover{
    color: blue;
}

.react-sanfona-item{
    padding: 15px 0 15px 0;
}


.accordionTitles{
    color: pink;
}

.fa-arrow-right {
    font-size: 30px;
    margin-left: 50px;
}

.fa-arrow-left{
    font-size: 30px;
    margin-right: 50px;
}

.fa:hover{
    color: rgb(8, 123, 199);
}

#imgInfo{
    width: 50%;
    margin: auto;
}


