#navbar{
    display: -ms-flexbox;
    display: flex;
    background-color: black;
    width: 100vw;
}

#titleDiv{
    -ms-flex: 3 1;
        flex: 3 1;
    color: white;
    text-align: left;    
}

#navMenu{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    text-transform: uppercase;
    font-size: 0.8em;
    display: -ms-flexbox;
    display: flex;
    color: white;
    font-weight: 700;
    text-decoration: none;
    max-height: 10vh;
}

#name{
    -ms-flex: 2 1;
        flex: 2 1;
    font-size: 3em;
    color: #3e89b7;
    margin-left: 30px;
}

#navMenu > div{
    cursor: pointer;
    -ms-flex: 1 1;
        flex: 1 1;
    border-bottom: solid 4px rgba(0,0,0,0);
}

#navMenu a {
    text-decoration: none;
    color: white;
}

#navMenu a:active{
    color: rgb(26, 23, 201);
}

#navMenu > div:hover{
    border-bottom: solid 4px rgb(50, 119, 146);
}

#res a {
    text-decoration: none;
    color: white;
}




